body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  font: 14px/18px "Asap", sans-serif;
  background: #f9f9f9;
  color: #333;
  height: 100%; }
  @media (min-width: 992px) {
    body {
      max-width: 1366px;
      width: 100%;
      margin: 0 auto;
      float: none; } }
  body .jumbotron {
    padding: 22px 30px 12px;
    margin: 0px auto;
    background: #9575CD;
    color: #fffaf0; }
    @media (min-width: 992px) {
      body .jumbotron {
        padding: 20px 30px; } }
    body .jumbotron .row-header {
      margin: 0px auto; }
      body .jumbotron .row-header h1 {
        font-size: 28px; }
        @media (min-width: 992px) {
          body .jumbotron .row-header h1 {
            font-size: 40px; } }
  body .home-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column; }
    @media (min-width: 992px) {
      body .home-container {
        flex-direction: row; } }
    body .home-container .data-wrapper {
      padding-top: 20px;
      background: #077dc3;
      margin-top: 20px; }
      @media (min-width: 992px) {
        body .home-container .data-wrapper {
          flex-grow: 1;
          width: 15%;
          margin-top: 0; } }
      body .home-container .data-wrapper .dataContainer {
        display: flex;
        max-width: 195px;
        width: 100%;
        margin: 0 auto;
        float: none;
        margin: 10px auto;
        padding: 10px;
        justify-content: space-evenly; }
    @media (min-width: 992px) {
      body .home-container .loan-container {
        flex-grow: 1;
        width: 65%; } }
  body .calculator {
    box-shadow: 15px 20px 70px -10px;
    padding: 2.5rem 2rem 2rem;
    max-width: 19rem;
    width: 100%;
    margin: 0 auto;
    float: none;
    margin-top: 20px; }
    @media (min-width: 992px) {
      body .calculator {
        max-width: 20rem;
        width: 100%;
        margin: 0 auto;
        float: none;
        margin-top: 30px; } }
    body .calculator h4 {
      padding: 10px 0;
      text-align: center; }
    body .calculator .input-range__label-container,
    body .calculator .input-range__label--min,
    body .calculator .input-range__label--max {
      display: none; }
    body .calculator .details-container h6 {
      font-weight: 700; }
      body .calculator .details-container h6 .blue {
        color: #077dc3; }
  body .footer {
    flex-shrink: 0;
    background-color: #333;
    color: #fff;
    text-align: center;
    font-size: 20px;
    line-height: 40px;
    width: 100%;
    padding: 20px 0px 20px 0px; }
    body .footer .btn {
      color: #fff;
      font-size: 14px; }
      @media (min-width: 992px) {
        body .footer .btn {
          font-size: 16px; } }
    body .footer h2 {
      font-size: 20px; }
      @media (min-width: 992px) {
        body .footer h2 {
          font-size: 28px; } }

#root {
  display: flex;
  flex-direction: column;
  height: 100%; }

.App {
  flex: 1 0 auto; }

html {
  height: 100%; }

